/*--   -*/
body{
  min-width: $min-width;
  font-family: $base-font-family;
  background: $base-bg;
  line-height: $base-line-height;
  min-height: 100vh;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}
.container{
  min-width: $min-width;
  width: $max-width;
  margin:0 auto;
  padding: 0;
}
.main-wrapper{
  padding: 0 0 0 0;
  min-width: $min-width;
  min-height: 100vh;
  width: 100%;
  position: relative;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}


