@media screen and (max-width: 1200px) {
  .container {
    width: 970px;
  }
}

@media screen and (max-width: 991px) {
  .container {
    width: 750px;
  }
}

@media screen and (max-width: 767px) {
  .container {
    width: 450px;
  }

  .wrap-text-block img {
    display: block;
    float: none !important;
    margin: 0 auto 15px !important;
  }
}

@media screen and (max-width: 479px) {
  .container {
    width: 310px;
  }
}
